  -webkit-transform: translate(-70%, -50%);
          transform: translate(-70%, -50%);
  -webkit-animation: spin 1.5s ease-out infinite;
          animation: spin 1.5s ease-out infinite;
  height: 2rem;
  -webkit-filter: invert(1);
          filter: invert(1);
